import React, {Fragment} from 'react';
import '../../App.css';
import ButtonReusable from '../Reusable/Button/Button'
import {Row, Col, Container, Button} from 'react-bootstrap'

function Hero() {
  return (
    <div className='background-image-hero'>
      <Row>
        <Col xs='12' lg='12' className='mx-auto'>
          <h1 className='extra-large align-center poppins-italics-large white-text hero-margin-top'>Ensuring safe food access<br></br> to millions.</h1>
        </Col>
      </Row>
      <Row >
        {/* <ButtonReusable class='big-button blue-background align-center button-margin-top' text='Learn More'></ButtonReusable> */}
        <Button className='big-button blue-background border-radius poppins-italics align-center button-margin-top'>
          Learn More
        </Button>
      </Row>
    </div>
  );
}

export default Hero;
