import React, {Fragment} from 'react';
import {Col, Row, Button} from 'react-bootstrap';
import '../../App.css';

function About() {
    return (
        <Fragment>
            <Row className='padded'>
                <Col  xs={8} md={8}>
                    <h1 className='poppins'>We believe that young people are the leaders we need to create the healthy, just and vibrant schools all of our communities deserve.</h1>
                </Col>
                <Col xs={4} md={4}>
                    <Button className='border-radius poppins-italics' style={{margin:'50px 200px'}}>
                        Donate
                    </Button>
                </Col>
            </Row>
            <Row className='padded'>
                <Col xs={4} md={4}>
                    <h1>Test</h1>
                </Col>
                <Col xs={4} md={4}>
                    <h1>About</h1>
                </Col>
                <Col xs={4} md={4}>
                    <h1>Mission</h1>
                </Col>
            </Row>
        </Fragment>
    );
  }
  
  export default About;