import React, {Fragment} from 'react';
import '../../App.css';
import {Link, Route} from 'react-router-dom';
import {Navbar, Nav, NavLink, NavDropdown, NavbarBrand, Col, Container, Row, Button} from 'react-bootstrap';
import logo from '../../Assets/logo.png';


const Menu = (props) => {
  return (
    <Row style={{backgroundColor: '#000000'}}>
      <Col xs={2} md={2}>

      </Col>

      <Col xs={2} md={2}>
          <Link className='link-style' to='/'>
              <NavbarBrand className='nav-logo mx-auto' alt="logo"><Link to='/'><Nav.Link className='logo white-text'>unBox</Nav.Link></Link></NavbarBrand>
          </Link>
      </Col>

      <Col xs={4} md={4}>
        <Navbar  className='main   ' expand="lg">
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse  id="basic-navbar-nav">
            <Nav className="mx-auto">
              <Nav.Link><Link className='poppins-italics white-text link-style' to='/map'>MAP</Link></Nav.Link>
              <Nav.Link><Link className='poppins-italics white-text link-style' to='/questions'>QUESTIONS</Link></Nav.Link>
              <Nav.Link><Link className='poppins-italics white-text link-style' to='/mission'>MISSION</Link></Nav.Link>
            </Nav>
          </Navbar.Collapse >
        </Navbar>
      </Col>
      <Col className='padding-nav' xs={4} md={4}>
        <Button className='border-radius nav-margins poppins-italics'>
          Donate
        </Button>
      </Col>
     </Row>
  );
}

export default Menu;
